.boton-contenedor {
  display: flex;
  flex: 1 1;
  padding: 12px;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 38px;
  background-color: #1b1b32;
  color: white;
  border-radius: 15px;
  border: 2px solid white;
  margin: 4px;
  cursor: pointer;
  user-select: none;
  }

  .boton-contenedor:hover{
    background: #5a01a7;
  }

  .operador{
    background-color: #00471b;

  }
  
.operador:hover{
  background-color: #ff6600;
}